*{
  margin: 0;
  padding: 0;
  font-family: 'dosis', sans-serif;
  letter-spacing: 1.2px;
  line-height: 1.6;
}

.App {
  margin: auto;
  max-width: 1200px;
  min-width: 600px;
  text-align: center;
}

/* nav */
.nav {
  margin: auto;
  display: flex;
  justify-content: center;
  margin-bottom: 3.5em;
}
.nav a {
  display: block;
  height: 100%;
  width: 100%;
  text-decoration: none;
  color: black;
  font-weight: bolder;
}
.nav ul {
  margin: 1.5em auto;
  display: flex;
  border-radius: 5px;
  list-style-type: none;
}
.nav ul li {
  display: inline;
  background-color: white;
  padding: 0.5em 2em;
  border-radius: 5px;
}
.nav ul li:hover {
  background-color: rgb(190, 245, 255);
}
.nav ul li.last {
  border-right: none;
}

/* markdown previewer*/
#editHeading {
  background-color: #b2deeb;
  padding: 0.2em;
}
#outputHeading {
  background-color: #9aecdf;
  padding: 0.2em;
  margin-bottom: 0.2em;
}
textarea {
  margin: 2.5em auto;
  padding: 0.3em 0.5em;
  width: 500px;
  height: 200px;
}
#preview {
  width: 500px;
  margin: auto;
  text-align: left;
}

#preview ul, #preview ol {
  list-style-position: inside;
}


/* calc app*/
#calcGrid {
  margin: auto;
  display: grid;
  grid-template-rows: repeat(7, 1fr);
  grid-template-columns: repeat(4, 1fr);
  padding: 0.75em;
  grid-gap: 5px;
  width: 30vw;
  height: 50vw;
  width: 300px;
  height: 500px;
  border: 2px solid slategray;
  border-radius: 3px;
  background-color: rgb(50, 49, 56);
  grid-template-areas: 
  "a a a a"    /*equation    */
  "b b b b"    /*output      */
  "c c d e"    /*AC / *      */
  "f g h i"    /*7 8 9 -     */
  "j k l m"    /*4 5 6 +     */
  "n o p q"    /*1 2 3 =     */
  "r r s q";    /* 0  .      */
}
.gridItem {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(97, 161, 221);
  border: 1px outset rgb(200, 242, 255);
  border-radius: 6px;
  font-weight: bolder;
}
.gridItem:not(.noHilite):hover{
  border: 2px solid white;
  cursor: pointer;
}
#input {
  grid-area: a;
  background-color: rgb(255, 255, 242);
  border: 1px inset darkgoldenrod;
  border-radius: 2px;
  margin-bottom: 0.75em;
  font-weight: normal;
}
#display_calc {
  grid-area: b;
  background-color: rgb(255, 255, 162);
  border: 1px inset rgb(255, 183, 0);
  border-radius: 2px;
  margin-bottom: 0.75em;
  font-size: 1.2rem;
}
#clear {
  grid-area: c;
  background-color: rgb(255, 104, 104);
}
#divide, #multiply, #subtract, #add {
  background-color: rgb(27, 26, 51);
  color: white;
}
#divide {
  grid-area: d;
}
#multiply {
  grid-area: e;
}
#seven {
  grid-area: f;
}
#eight {
  grid-area: g;
}
#nine {
  grid-area: h;
}
#subtract {
  grid-area: i;
}
#four {
  grid-area: j;
}
#five {
  grid-area: k;
}
#six {
  grid-area: l;
}
#add {
  grid-area: m;
}
#one {
  grid-area: n;
}
#two {
  grid-area: o;
}
#three {
  grid-area: p;
}
#equals {
  grid-area: q;
  background-color: rgb(127, 47, 255);
}
#zero {
  grid-area: r;
}
#decimal {
  grid-area: s;
}



/* quote app */
#quote-box {
  margin: 1.5em auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 210px;
  border-top: 1px solid gray;
  border-bottom: 1px solid gray;
  background-color: rgba(182, 237, 255, 0.15);
}

#author {
  margin: 0.5em auto 1em;
}
#tweet-quote {
  display: block;
}
#new-quote {
  padding: 0.5em 1em;
  background-color: rgb(182, 237, 255);
  border: 2px solid rgb(31, 136, 223);
  border-radius: 3px;
}
#new-quote:hover {
  cursor: pointer;
  background-color: rgb(210, 252, 255);
}

/* pomodoro clock */
.btnTime {
  margin: 5px;
  width: 80px;
  height: 25px;
  color: black;
  background-color: rgb(140, 198, 255);
  border: 1px solid  rgb(38, 88, 138);
  border-radius: 4px;
  cursor: pointer;
}
.btnControls {
  width: 45%;
  height: 25px;
  margin: 3px;
}
#session-label, #break-label, #session-control  {
  margin: auto;
  width: 100%;
  padding: 1em 0;
}
#session-label {
  background-color: rgb(192, 192, 192);
  border-bottom: 2px solid rgba(0,0,50,0.25);
}
#break-label {
  background-color: rgb(96, 255, 221);
}
#pomodoro {
  display: flex;
  flex-direction: column;
  margin: auto;
  width: 250px;
  border: 2px outset rgb(215, 224, 233);
  border-radius: 4px;
  box-shadow: 4px 4px 8px 10px  rgba(0,0,30,0.2);
}
#pomodoro p {
  line-height: 2.2;
}


/* drum app */
#display {
  display: flex;
  min-width: 500px;
  justify-content: center;
  align-items: center;
}
.drum-pad {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 100px;
  border: 1px solid black;
  border-radius: 3px;
  cursor: pointer;
}
/*copied from sass files*/
.drum-pad#audio_1 {
  background-color: #ffa2a2;
}
.drum-pad#audio_2 {
  background-color: #ffd1a2;
}
.drum-pad#audio_3 {
  background-color: #ffffa2;
}
.drum-pad#audio_4 {
