}
.drum-pad#audio_5 {
  background-color: #a2ffa2;
}
.drum-pad#audio_6 {
  background-color: #a2ffd1;
}
.drum-pad#audio_7 {
  background-color: #a2ffff;
}
.drum-pad#audio_8 {
